.main-weather{
    margin-top: 0;
}
.main-weather-body{
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-left: -100px;
}
.main-weather-text{
    text-align: left;
}
/* .main-weather-text > *{
    margin: 7rem;
} */
.weather-text{
    line-height: 0.7;
}
.city{
    color: #ffffffe0;
    font-size: 37px;
    font-weight: 600;
}
.rainfall{
    color: #d3d3d375;
}
.temperature{
    color: #fffffff6;
    margin-top: 2.7rem;
    font-size: 52px;
    font-weight: 800;
}
.main-weather-image{
    align-items: center;
}
.weather-img{
    width: 270px;
    height: auto;
}
.Hourly-forecast-body{
    margin-top: -20px;
    background-color: #292c3b;
    margin-left: 75px;
    border-radius: 15px;
    width: 76%;
    height: 180px;
    padding: 10px;
}
.todays-forecast{
    color: #d3d3d39a;
    text-align: left;
    padding-left: 15px;
    font-size: 13px;
    font-weight: 500;
}
.hourly-forecast{
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: -10px;
}
.forecast-div{
    padding: 0;
    margin-top: 7px;
    line-height: 0.5;
}
.time{
    color: #d3d3d39a;
    font-size: 13px;
    font-weight: 500;
}
.forecast-img{
    width: 55px;
    padding-top: -7px;
}
.forecast-temp{
    color: #ffffffe0;
    padding-bottom: 5px;
    font-size: 18px;
}
.underline{
    width: 0.2px;
    height: 110px;
    background-color: #d3d3d31c;
}
.underline:last-child{
    display: none;
}
.main-weather-conditions{
    background-color: #292c3b;
    margin-left: 75px;
    border-radius: 15px;
    width: 76%;
    height: 165px;
    padding: 10px;
    margin-top: 12px;
}
.main-weather-header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: -1px;
}
.air-conditions{
    color: #d3d3d39a;
    padding-left: 15px;
    font-size: 12px;
    font-weight: 500;
}
.see-btn{
    cursor: pointer;
    margin-right: 18px;
    background-color: rgb(43, 118, 204);
    color: #ffffffe0;
    border: none;
    /* padding: 7px; */
    border-radius: 30px;
    font-size: 11px;
    font-weight: 400;
    width: 70px;
    height: 21px;
    padding-bottom: 2px;
    padding-left: 7px;
}
.main-weather-grid{
    display: grid;
    grid-template-columns: auto auto;
    margin-left: 17px;
    margin-top: -10px;
    text-align: left;
    line-height: 0;
    row-gap: 0;
    column-gap: 260px;
}
.main-weather-grid-item p{
    color: #d3d3d39a;
    font-size: 14px;

}
.main-weather-grid-item i{
    font-size: 16px;
}
.main-weather-grid-item label{
    margin-left: 5px;
}
.main-weather-grid-item h2{
    font-size: 22px;
    color: #d3d3d3ee;
    padding-left: 20px;
}
.main-weather-grid-item:nth-child(2) h2{
    padding-left: 24px;
}
.main-weather-grid-item:nth-child(4) h2{
    padding-left: 24px;
}
.panel{
    height: 120px !important;
}

@media only screen and (max-width: 992px) {
    .main-weather-body{
        margin-top: 0;
        margin-left: -50px;
    }
    .city{
        font-size: 33px;
    }
    .rainfall{
        font-size: 13px;
    }
    .temperature{
        font-size: 47px;
    }
    .weather-text{
        line-height: 0.5;
    }
    .weather-img{
        width: 250px;
        height: auto;
        position: relative;
        left: -150px;
        top: -10px;
    }
    .Hourly-forecast-body{
        margin-top: -30px;
        margin-left: 30px;
        height: 160px;
        width: 63%;
    }
    .todays-forecast{
        font-size: 11px;
    }
    .hourly-forecast{
        margin-top: -10px;
        margin-left: 15px;
    }
    .time{
        font-size: 12px;
    }
    .forecast-img{
        width: 50px;
        padding-top: -10px;
    }
    .forecast-temp{
        font-size: 17px;
        padding-top: -10px;
    }
    .forecast-div{
        line-height: 0;
        margin-top: 15px;
    }
    .underline{
        height: 90px;
    }
    .main-weather-conditions{
        margin-left: 30px;
        height: 160px;
        width: 63%;
    }
    .air-conditions{
        font-size: 11px;
    }
    .main-weather-grid-item p{
        font-size: 13px;
    }
    .main-weather-grid-item i{
        font-size: 15px;
    }
    .main-weather-grid-item h2{
        font-size: 20px;
    }
    .main-weather-grid{
        column-gap: 10px;
    }
}
@media only screen and (max-width: 425px) {
    .main-weather-body{
        margin-top: -20px;
        margin-left: 30px;
    }
    .city{
        font-size: 32px;
    }
    .rainfall{
        font-size: 12px;
    }
    .temperature{
        font-size: 46px;
    }
    .weather-img{
        width: 260px;
        height: auto;
        position: relative;
        left: 10px;
        top: -10px;
    }
    .Hourly-forecast-body{
        margin-top: -40px;
        margin-left: 25px;
        height: 140px;
        width: 83%;
    }
    .todays-forecast{
        padding-top: -20px;
        padding-left: 5px;
    }
    .hourly-forecast{
        margin-top: -20px;
        margin-left: 10px;
    }
    .forecast-img{
        padding-top: -10px;
    }
    .forecast-temp{
        margin-top: 15px;
    }
    .underline{
        margin-top: 7px;
        height: 95px;
    }
    .main-weather-conditions{
        margin-left: 28px;
        height: 140px;
        width: 83%;
    }
}
@media only screen and (max-width: 375px) {
    .main-weather-body{
        margin-left: 70px;
    }
    .weather-text{
        line-height: 0.7;
    }
    .rainfall{
        line-height: 1.2;
        width: 100%;
    }
    .weather-img{
        width: 250px;
    }
    .Hourly-forecast-body{
        margin-left: 23px;
        width: 85%;
    }
    .main-weather-conditions{
        margin-left: 24px;
        width: 85%;
    }
}