.search-form{
    margin-top: 20px;
    margin-left: -100px;
}
.search-form input{
    width: 70%;
    height: 30px;
    border: 1px solid #292c3b;
    background-color: #292c3b;
    padding: 5px;
    border-radius: 10px;
    color: #ffffff8a;
}
.search-form input::placeholder{
    color: #ffffff8a;
}
.search-form input:focus{
    outline: none;
    border-color: #ffffff8a;
}

@media only screen and (max-width: 992px) {
    .search-form{
        margin-left: -200px;
    }
    .search-form input{
        width: 50%;
    }
}
@media only screen and (max-width: 425px) {
    .search-form{
        margin-left: -10px;
    }
    .search-form input{
        width: 85%;
        height: 25px;
    }
}
@media only screen and (max-width: 375px) {
    .search-form{
        margin-left: 10px;
    }
    .search-form input{
        width: 90%;
    }
}