.main-forecast-div{
    width: 29%;
    height: 638px;
    background-color: #292c3b;
    position: absolute;
    top: 75px;
    right: 17px;
    border-radius: 15px;
    /* display: none; */
}
.day-forecast{
    text-align: left;
    padding-top: 17px;
    padding-left: 29px;
    color: #d3d3d39a;
    font-size: 12px;
    font-weight: 500;
}
.daily-forecast{
    margin-top: 12px;
}
.daily-forecast-item{
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.daily-forecast-item p{
    color: #d3d3d39a;
    font-size: 12px;
    margin-left: -30px;
}
.image-daily-forecast{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-left: -35px;
    text-align: center;
}
.img-daily{
    width: 47px;
}
.image-daily-forecast p{
    margin-left: 7px;
    color: #d3d3d3ee;
    font-size: 12px;
    font-weight: 500;
}
.temperature-max{
    margin-right: -20px;
    font-size: 12px;
    font-weight: 500;
}
.temperature-max span{
    color: #d3d3d3ee;
}
.div-underline{
    width: 325px;
    height: 0.2px;
    background-color: #d3d3d31c;
    margin-left: 30px;
    margin-top: 19px;
    margin-bottom: 19px;
}
@media only screen and (max-width:992px) {
    .main-forecast-div{
        width: 28%;
        height: 600px;
        top: 60px;
        right: 7px;
    }
    .day-forecast{
        font-size: 11px;
        padding-top: 10px;
        padding-left: 10px;
    }
    .daily-forecast{
        margin-top: 5px;
    }
    .daily-forecast-item p{
        font-size: 11px;
        margin-left: -25px;
    }
    .image-daily-forecast{
        margin-left: -40px;
    }
    .image-daily-forecast p{
        margin-left: 5px;
    }
    .div-underline{
        width: 195px;
        margin-left: 9px;
        margin-top: 15px;
        margin-top: 15px;
    }
}
@media only screen and (max-width:425px) {
    .main-forecast-div{
        position: static;
        width: 87%;
        height: 560px;
        margin-left: 32px;
    }
    .day-forecast{
        padding-top: 25px;
        padding-left: 25px;
    }
    .daily-forecast{
        margin-top: 2px;
        margin-left: -10px;
    }
    .div-underline{
        width: 310px;
        margin-left: 35px;
        margin-top: 10px;
        margin-top: 10px;
    }
}
@media only screen and (max-width:375px) {
    .main-forecast-div{
        position: static;
        margin-left: 26px;
        width: 89%;
    }
    .day-forecast{
        padding-left: 23px;
    }
    .daily-forecast{
        margin-left: -6px;
    }
    .div-underline{
        width: 290px;
        margin-left: 25px;
    }
}